import React from "react";
import GoogleMapReact from "google-map-react";
import { MapContainer } from "../styles/styles";
import RaptorMarker from "../markers/RaptorMarker";
import whiteMap from "../styles/whiteMap.json";

const Map = ({ history, mapKey, markers, places }) => {
  if (markers.length === 0) {
    return <div>loading markers...</div>;
  }
  console.log(markers);
  return (
    <MapContainer>
      <GoogleMapReact
        defaultCenter={places.ppark}
        defaultZoom={17}
        // bootstrapURLKeys={{ key: mapKey }}
        options={{ styles: whiteMap }}
        onChildClick={p => {
          history.push(`/pop/${p}`);
        }}
      >
        {markers.map(m => {
          console.log(m.hash);
          return (
            <RaptorMarker
              key={m.hash}
              id={m.hash}
              found={m.found}
              lat={m.lat}
              lng={m.lng}
            />
          );
        })}
      </GoogleMapReact>
    </MapContainer>
  );
};

export default Map;
